import PropTypes from 'prop-types';
// import s from './PhoneForm.module.css';
import s from './ContactList.module.css';


const PhoneBookForm = ({ onSetUserInfo, onAddContact, name, number }) => {
  return (
    <form className={s.form} onSubmit={onAddContact} action="">
      <label className={s.label} htmlFor="">{/* htmlFor –ø–æ–º–æ–≥–∞–µ—Ç —á–∏—Ç–∞–ª–∫–∞–º –∏ –¥—Ä—É–≥–∏–º –≤—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω—ã–º –∏–Ω—Å—Ç—Ä—É–º–µ–Ω—Ç–∞–º. */}
        <p>Name</p>
        <input
          className={s.input}
          onInput={onSetUserInfo}
          type="text"
          name="name"
          value={name}
          pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
          title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
          required
          placeholder="Ivan Ivanov"
        />
      </label>
      <label className={s.label} htmlFor="">
        <p>Number</p>
        <input
          className={s.input}
          onInput={onSetUserInfo}
          type="tel"
          name="number"
          value={number}
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –∫—Ä—É–≥–ª—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
          required
          placeholder="111-11-11"
        />
      </label>
     <button className={s.btn} type="submit">
        Add contact
      </button>
    </form>
  );
};

PhoneBookForm.propTypes = {
  onAddContact: PropTypes.func.isRequired
};

export default PhoneBookForm;
