import Container from './components/Container/Container';
import PhoneBookForm from './components/Form/PhoneBookForm';
import PhoneBookList from './components/List/PhoneBookList';
import PhonebookFilter from './components/Filter/PhonebookFilter';
import filterContacts from './helpers/filterContacts';
import shortId from 'shortid';

import { useState, useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//??- –∏–ª–∏. –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –ø—Ä–∞–≤—É—é —á–∞—Å—Ç—å –µ—Å–ª–∏ undefined –∏–ª–∏ null.
function App() {
  //–ª–µ–Ω–∏–≤–∞—è –∏–Ω–∏—Ü–∏–∞–ª-—Ü–∏—è. —Å–æ—Å—Ç–æ—è–Ω–∏—è –≤—ã–ø–æ–ª–Ω–∏—Ç—Å—è –æ–¥–∏–Ω —Ä–∞–∑ –ø—Ä–∏ –ø–µ—Ä–≤–æ–º —Ä–µ–Ω–¥–µ—Ä–µ () => {}
  const [contacts, setContacts] = useState(() => {
    return JSON.parse(localStorage.getItem('contacts')) ?? [];
  });
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');
  const [filter, setFilter] = useState('');

  //–µ—Å–ª–∏ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –∏–∑–º–µ–Ω—è—é—Ç—Å—è
  useEffect(() => {
    window.localStorage.setItem('contacts', JSON.stringify(contacts));
  }, [contacts]); //–∑–∞–≤–∏—Å–∏–º–æ—Å—Ç—å.

  const handleChangeInput = e => {
    const { name, value } = e.target;
    switch (name) {
      case 'name':
        setName(value);
        break;
      case 'number':
        setNumber(value);
        break;
      case 'filter':
        setFilter(value);
        break;

      default:
        return;
    }
  };

  const handleAddContact = e => {
    e.preventDefault();

    if (contacts.some(el => el.name === name)) {
      toast(`ü§î ${name} is already in contacts.`);
      return;
    }
    if (contacts.some(el => el.number === number)) {
      toast(`ü§î ${number} is already in contacts.`);
      return;
    }
    if (!/\d{3}[-]\d{2}[-]\d{2}/g.test(number)) {
      toast.error('Enter the correct number phone!');
      return;
    }
    const contact = {
      name,
      number,
      id: shortId.generate(),
    };
    setContacts(prev => [...prev, contact], setName(''), setNumber(''));
  };

  const handleDeleteContact = e => {
    setContacts(contacts.filter(el => el.id !== e.target.id));
  };

  // const handleChangeFilter = e => {
  //   setFilter(e.target.value);
  // };

  const contactsArray = filterContacts(contacts, filter);
  return (
    <Container>
      <h1>Phonebook</h1>
      <PhoneBookForm
        contacts={contactsArray}
        name={name}
        number={number}
        onAddContact={handleAddContact}
        onSetUserInfo={handleChangeInput}
      />
      <h2>Contacts</h2>
      {contacts.length > 1 && (
        <PhonebookFilter filterValue={filter} onSetFilter={handleChangeInput} />
      )}
      {contacts.length > 0 ? (
        <PhoneBookList
          onDeleteContact={handleDeleteContact}
          contacts={contactsArray}
        />
      ) : (
        <p>Your phonebook is empty. Please add contact.</p>
      )}
      <ToastContainer autoClose={3700} />
    </Container>
  );
}
export default App;
